.about{
  display: flex;
  flex-direction: column;
  padding-top: 100px;
  padding-bottom: 120px;
  background: #161616;
}
.about-title{
  display: flex;
  justify-content: center;
  font-size: 40px;
  line-height: 52px;
  letter-spacing: -0.02em;
  color: #FFF;
  padding-bottom: 60px;
}
.form{
  display: flex;
  flex-direction: column;
}
.form-item{
  display: flex;
  align-items: center;
  padding-bottom: 70px;
}
.form-item__value{
  font-family: 'Roboto';
  font-weight: 500;
  font-size: 20px;
  line-height: 23px;
  color: #F47821;
  padding: 20px 26px;
  border: 1px solid #4b4b4b;
  border-radius: 50%;
  margin-right: 28px;
}
// content
.form-item__content{
  display: flex;
  flex-direction: column;
  max-width: 575px;
}
.content-label{
  font-size: 16px;
  line-height: 25px;
  letter-spacing: -0.01em;
  color: #FFF;
  padding-bottom: 24px;
}
.content-input{
  padding-bottom: 12px;
  border-bottom: 2px solid #F7F8FA;
  width: 575px;
  font-size: 24px;
  line-height: 35px;
  letter-spacing: -0.01em;
  color: #FFF;
  opacity: 0.3;
  text-overflow:ellipsis;
}
// btn
.form-btn{
  text-transform: uppercase;
  font-family: 'Gilroy';
  font-size: 14px;
  line-height: 17px;
  letter-spacing: 0.04em;
  color: #F47821;
  padding: 20px 245px 16px 246px;
  background: #fff;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.12);
  border-radius: 111px;
  margin-left: 80px;
}
.form-btn:hover,
.form-btn:active{
  cursor: pointer;
  background: #F47821;
  color: #fff;
}
// form error
.err{
  background: rgba(red, .4);
}




// media
@media screen and (max-width: 720px){

  .about{
    padding-top: 80px;
    padding-bottom: 100px;
    max-width: 95%;
  }
  .about-title{
    font-size: 32px;
    line-height: 42px;
    padding-bottom: 50px;
  }
  .form-item{
    padding-bottom: 60px;
  }
  .form-item__value{
    font-size: 18px;
    line-height: 19px;
    padding: 18px 22px;
    margin-right: 25px;
  }
  // content
  .form-item__content{
    max-width: 80%;
  }
  .content-label{
    font-size: 16px;
    line-height: 20px;
    padding-bottom: 20px;
  }
  .content-input{
    padding-bottom: 12px;
    width: 450px;
    font-size: 20px;
    line-height: 28px;
  }
  // btn
  .form-btn{
    font-size: 14px;
    line-height: 15px;
    padding: 20px 145px 16px 146px;
    margin-left: 80px;
  }
}

