    line-height: 16px;
    padding: 16px 20px;
    margin-right: 22px;
  }
  // content
  .form-item__content{
    max-width: 66%;
  }
  .content-label{
    font-size: 15px;
    line-height: 18px;
    padding-bottom: 18px;
  }
  .content-input{
    padding-bottom: 10px;
    width: 350px;
    font-size: 18px;
