.icon{
  fill: #F47821;
  width: 14px;
  height: 14px;
  flex-shrink: 0;
}
.icon-advantages{
  fill: #F47821;
  width: 52px;
  height: 52px;
  flex-shrink: 0;
}
// media
@media screen and (max-width: 930px){

  .icon-advantages{
    width: 48px;
    height: 48px;
  }
}
@media screen and (max-width: 720px){

  .icon-advantages{
    width: 44px;
    height: 44px;
  }
}
@media screen and (max-width: 420px){

  .icon-advantages{
    width: 38px;
    height: 38px;
  }
}