.motivation{
  position: relative;
  padding-bottom: 464px;
  background: #F7F5FA;
}
.motivation-back{
  display: flex;
}
.motivation-items{
  position: absolute;
  bottom: 80px;
  left: calc(50% - (972px / 2));
  display: flex;
  flex-wrap: wrap;
  background: #fff;
}
.motivation-item{
  border: 1px solid #EDEDED;
  width: calc(100% / 2);
  padding: 50px 70px 61px 60px;
}
.motivation-item__title{
  text-transform: uppercase;
  font-size: 12px;
  color: #F47821;
  line-height: 19px;
  letter-spacing: .04em;
  padding-bottom: 12px;
}
.motivation-item__text{
  font-size: 24px;
  line-height: 30px;
  letter-spacing: -.03em;
}


// media
@media screen and (max-width: 1025px){

  .motivation-items{
    left: 2%;
    right: 2%;
    bottom: 65px;
  }
}

@media screen and (max-width: 950px){

  .motivation{
    padding-bottom: 400px;
  }
  .motivation-item{
    padding: 40px 60px 51px 50px;
  }
  .motivation-item__text{
    font-size: 20px;
    line-height: 25px;
  }
}

@media screen and (max-width: 800px){

  .motivation{
    padding-bottom: 330px;
  }
  .motivation-item{
    padding: 30px 50px 41px 40px;
  }
  .motivation-item__title{
    line-height: 16px;
    padding-bottom: 10px;
  }
  .motivation-item__text{
    font-size: 18px;
    line-height: 20px;
  }
}

@media screen and (max-width: 690px){

  .motivation{
    padding-bottom: 600px;
  }
  .motivation-item{
    width: 100%;
    padding: 40px 80px 40px 70px;
  }
}

@media screen and (max-width: 525px){

  .motivation-item{
    padding: 40px 30px 40px 70px;
  }
}

@media screen and (max-width: 480px){

  .motivation{
    padding-bottom: 490px;
  }
  .motivation-items{
    bottom: 30px;
  }
  .motivation-item{
    padding: 30px 30px 30px 40px;
  }
  .motivation-item__title{
    font-size: 10px;
  }
  .motivation-item__text{
    font-size: 16px;
    line-height: 18px;
  }
}

@media screen and (max-width: 410px){

  .motivation{
    padding-bottom: 370px;
  }
  .motivation-item{
    padding: 20px 20px 20px 30px;
  }
  .motivation-item__title{
    padding-bottom: 5px;
  }
  .motivation-item__text{
    font-size: 14px;
    line-height: 16px;
  }
}

@media screen and (max-width: 345px){

  .motivation{
    padding-bottom: 390px;
  }
  .motivation-items{
    bottom: 20px;
  }
}

@media screen and (max-width: 345px){

  .motivation-items{
    bottom: 10px;
  }
}