.motivation-back{
  max-width: 1440px;
  margin: 0 auto;
}

.advantages{
  max-width: 1140px;
  margin: 0 auto;
  padding: 0 10px;
}

.head,
.motivation-items,
.identity{
  max-width: 972px;
  margin: 0 auto;
}

.identity{
  padding: 0 100px;
}

.about{
  max-width: 690px;
  margin: 0 auto;
  padding: 0 10px;
}

/* container */
.motivation-back,
.advantages,
.about{
  position: relative;
}

.motivation-back::after,
.advantages::after,
.about::after{
  content: '';
  display: block;
  height: 100%;
  width: 100vw;
  background-color: inherit;
  position: absolute;
  top: 0;
  z-index: -1;
  margin: auto;
  left: -50vw;
  right: -50vw;
}