.advantages{
  background: #F7F5FA;
  padding-top: 120px;
  padding-bottom: 160px;
}
.advantages-title{
  display: flex;
  justify-content: center;
  font-size: 40px;
  line-height: 50px;
  letter-spacing: -0.02em;
  padding-bottom: 60px;
}
.advantages-items{
  .row-flex();
}
.advantages-item{
  .col();
  .size(3);
}
.item-title{
  position: relative;
  padding-top: 30px;
  padding-bottom: 51px;
  font-size: 20px;
  font-weight: bold;
  line-height: 25px;
  color: #000;
}
.item-title::after{
  position: absolute;
  content: '';
  width: 24px;
  height: 2px;
  top: 78px;
  left: 0;
  background: #F47821;
}
.item-text{
  font-family: 'Roboto';
  font-size: 16px;
  line-height: 26px;
  color: #4E4D53;
}



// media
@media screen and (max-width: 930px){

  .advantages{
    padding-top: 90px;
    padding-bottom: 120px;
  }
  .advantages-title{
    font-size: 34px;
    line-height: 40px;
    padding-bottom: 45px;
  }
  .advantages-items{
    margin: 0 -22px;
  }
  .advantages-item{
    margin: 22px;
    width: calc(100% / 3 - 44px);
  }
  .item-title{
    padding-top: 25px;
    padding-bottom: 40px;
    font-size: 18px;
    line-height: 20px;
  }
  .item-title::after{
    top: 65px;
  }
  .item-text {
    font-size: 14px;
    line-height: 20px;
  }
}

@media screen and (max-width: 720px){

  .advantages{
    padding-top: 65px;
    padding-bottom: 80px;
  }
  .advantages-title{
    font-size: 28px;
    line-height: 30px;
  }
  .advantages-items{
    margin: 0 -20px;
  }
  .advantages-item{
    margin: 20px;
    width: calc(100% / 2 - 40px);
  }
  .item-title{
    padding-top: 20px;
    padding-bottom: 35px;
    font-size: 16px;
    line-height: 18px;
  }
  .item-title::after{
    top: 60px;
  }
  .item-text {
    line-height: 18px;
  }
}

@media screen and (max-width: 650px){

  .advantages{
    padding-top: 55px;
    padding-bottom: 60px;
  }
  .advantages-title{
    font-size: 25px;
    line-height: 25px;
  }
  .advantages-items{
    margin: 0 -15px;
  }
  .advantages-item{
    margin: 15px;
    width: calc(100% / 2 - 30px);
  }
}

@media screen and (max-width: 520px){

  .advantages{
    padding: 50px 0;
  }
  .advantages-title{
    font-size: 28px;
    line-height: 30px;
  }
  .advantages-items{
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  .advantages-item{
    width: 60%;
  }
  .item-title{
    padding-top: 25px;
    padding-bottom: 40px;
    font-size: 18px;
    line-height: 20px;
  }
  .item-title::after{
    top: 65px;
  }
  .item-text {
    font-size: 14px;
    line-height: 20px;
  }
}

@media screen and (max-width: 430px){

  .advantages{
    padding: 40px 10px;
  }
  .advantages-title{
    text-align: center;
    font-size: 24px;
    line-height: 20px;
    padding-bottom: 30px;
  }
  .item-title{
    padding-top: 20px;
    padding-bottom: 35px;
    font-size: 16px;
    line-height: 18px;
  }
  .item-title::after{
    top: 60px;
  }
  .item-text {
    line-height: 18px;
  }
}