body {
  font-family: 'DINPro';
  font-size: 16px;
  color: #222;
  margin: 0;
  padding: 0;
}
h1,
h2,
h3,
p,
ul,
li,
figure,
input,
label,
button {
  list-style: none;
  margin: 0;
}
img {
  width: 100%;
  height: 100%;
}
a {
  text-decoration: none;
}
input {
  border: none;
  background: inherit;
}
input[placeholder],
input::-moz-placeholder,
input:-moz-placeholder,
input:-ms-input-placeholder {
  text-overflow: ellipsis;
}
input:active,
input:focus,
input:hover {
  outline: 0;
  outline-offset: 0;
}
button {
  border: none;
  margin: 0;
  padding: 0;
  width: auto;
  overflow: visible;
  background: transparent;
  /* inherit font & color from ancestor */
  color: inherit;
  font: inherit;
  /* Normalize `line-height`. Cannot be changed from `normal` in Firefox 4+. */
  line-height: normal;
  /* Corrects font smoothing for webkit */
  -webkit-font-smoothing: inherit;
  -moz-osx-font-smoothing: inherit;
  /* Corrects inability to style clickable `input` types in iOS */
  -webkit-appearance: none;
}
button:active,
button:focus,
button:hover {
  outline: 0;
  outline-offset: 0;
}
.icon {
  fill: #F47821;
  width: 14px;
  height: 14px;
  flex-shrink: 0;
}
.icon-advantages {
  fill: #F47821;
  width: 52px;
  height: 52px;
  flex-shrink: 0;
}
@media screen and (max-width: 930px) {
  .icon-advantages {
    width: 48px;
    height: 48px;
  }
}
@media screen and (max-width: 720px) {
