    height: 38px;
  }
}
.motivation-back {
  max-width: 1440px;
  margin: 0 auto;
}
.advantages {
  max-width: 1140px;
  margin: 0 auto;
  padding: 0 10px;
}
.head,
.motivation-items,
.identity {
  max-width: 972px;
  margin: 0 auto;
}
.identity {
  padding: 0 100px;
}
.about {
  max-width: 690px;
  margin: 0 auto;
  padding: 0 10px;
}
/* container */
.motivation-back,
.advantages,
.about {
  position: relative;
}
.motivation-back::after,
.advantages::after,
.about::after {
  content: '';
  display: block;
  height: 100%;
  width: 100vw;
  background-color: inherit;
  position: absolute;
  top: 0;
  z-index: -1;
  margin: auto;
  left: -50vw;
  right: -50vw;
}
/* flex-box */
.head {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-top: 165px;
  padding-bottom: 142px;
}
.head-title {
  padding-bottom: 24px;
  font-size: 48px;
  line-height: 54px;
  color: #222;
}
.head-text {
  font-family: 'Roboto';
  line-height: 26px;
  color: #4A4556;
  padding-bottom: 48px;
}
.head-text__allot {
  color: #F47821;
  font-weight: bold;
}
.head-form {
  display: flex;
  align-items: center;
  text-transform: uppercase;
  font-family: 'Gilroy';
  line-height: 20px;
  letter-spacing: 0.02em;
  color: #222;
}
.head-form__back {
  padding: 20px 20px 17px 22px;
  background: #f8f8f8;
  border-radius: 50%;
}
.head-form__back:hover,
.head-form__back:active {
  cursor: pointer;
  background: #c7c5c5;
}
