.about {
  display: flex;
  flex-direction: column;
  padding-top: 100px;
  padding-bottom: 120px;
  background: #161616;
}
.about-title {
  display: flex;
  justify-content: center;
  font-size: 40px;
  line-height: 52px;
  letter-spacing: -0.02em;
  color: #FFF;
  padding-bottom: 60px;
}
.form {
  display: flex;
  flex-direction: column;
}
.form-item {
  display: flex;
  align-items: center;
  padding-bottom: 70px;
}
.form-item__value {
  font-family: 'Roboto';
  font-weight: 500;
  font-size: 20px;
  line-height: 23px;
  color: #F47821;
  padding: 20px 26px;
  border: 1px solid #4b4b4b;
  border-radius: 50%;
  margin-right: 28px;
}
.form-item__content {
  display: flex;
  flex-direction: column;
  max-width: 575px;
}
.content-label {
  font-size: 16px;
  line-height: 25px;
  letter-spacing: -0.01em;
  color: #FFF;
  padding-bottom: 24px;
}
.content-input {
  padding-bottom: 12px;
  border-bottom: 2px solid #F7F8FA;
  width: 575px;
  font-size: 24px;
  line-height: 35px;
  letter-spacing: -0.01em;
  color: #FFF;
  opacity: 0.3;
  text-overflow: ellipsis;
}
.form-btn {
  text-transform: uppercase;
  font-family: 'Gilroy';
  font-size: 14px;
  line-height: 17px;
  letter-spacing: 0.04em;
  color: #F47821;
  padding: 20px 245px 16px 246px;
  background: #fff;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.12);
  border-radius: 111px;
  margin-left: 80px;
