.identity{
  padding-top: 80px;
  padding-bottom: 100px;
}
.identity-title{
  font-size: 48px;
  line-height: 60px;
  letter-spacing: -0.02em;
  padding-bottom: 24px;
}
.identity-text{
  font-family: 'Roboto';
  font-size: 16px;
  line-height: 26px;
  color: #4E4D53;
}
.identity-text:not(:last-child){
  margin-bottom: 24px;
}



// media
@media screen and (max-width: 1024px){

  .identity{
    max-width: 972px;
    margin: 0 auto;
  }
}

@media screen and (max-width: 768px){

  .identity {
    padding-top: 60px;
    padding-bottom: 80px;
  }
  .identity-title{
    font-size: 38px;
    line-height: 40px;
    padding-bottom: 20px;
  }
  .identity-text{
    font-size: 14px;
    line-height: 20px;
  }
  .identity-text:not(:last-child){
    margin-bottom: 20px;
  }
}

@media screen and (max-width: 600px){

  .identity{
    padding: 45px 10px 60px 10px;
    max-width: 80%;
  }
  .identity-title{
    font-size: 32px;
    line-height: 30px;
    padding-bottom: 18px;
  }
  .identity-text{
    line-height: 18px;
  }
  .identity-text:not(:last-child){
    margin-bottom: 18px;
  }
}

@media screen and (max-width: 375px){

  .identity{
    padding: 30px 10px 40px 10px;
    max-width: 85%;
  }
  .identity-title{
    font-size: 28px;
    line-height: 25px;
    padding-bottom: 15px;
  }
  .identity-text{
    font-size: 13px;
    line-height: 16px;
  }
  .identity-text:not(:last-child){
    margin-bottom: 15px;
  }
}