.head{
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-top: 165px;
  padding-bottom: 142px;
}
.head-title{
  padding-bottom: 24px;
  font-size: 48px;
  line-height: 54px;
  color: #222;
}
.head-text{
  font-family: 'Roboto';
  line-height: 26px;
  color: #4A4556;
  padding-bottom: 48px;
}
.head-text__allot{
  color: #F47821;
  font-weight: bold;
}
.head-form{
  display: flex;
  align-items: center;
  text-transform: uppercase;
  font-family: 'Gilroy';
  line-height: 20px;
  letter-spacing: .02em;
  color: #222;
}
.head-form__back{
  padding: 20px 20px 17px 22px;
  background: #f8f8f8;
  border-radius: 50%;
}
.head-form__back:hover,
.head-form__back:active{
  cursor: pointer;
  background: #c7c5c5;
}


// media
@media screen and (max-width: 750px) {

  .head {
    padding-top: 125px;
    padding-bottom: 110px;
    text-align: center;
  }

  .head-title {
    padding-bottom: 20px;
    font-size: 38px;
  }
  .head-text{
    line-height: 22px;
    padding-bottom: 35px;
  }
  .head-form{
    font-size: 15px;
  }
  .head-form__back{
    display: flex;
    padding: 18px;
  }
}

@media screen and (max-width: 620px){

  .head {
    padding: 100px 10px 85px 10px;
  }
  .head-title {
    padding-bottom: 16px;
    font-size: 30px;
  }
  .head-text{
    font-size: 14px;
    padding-bottom: 25px;
  }
  .head-form{
    font-size: 12px;
  }
  .head-form__back{
    display: flex;
    padding: 16px;
  }
}

@media screen and (max-width: 500px){

  .head {
    padding: 80px 10px 60px 10px;
  }
  .head-title {
    padding-bottom: 10px;
    font-size: 26px;
    line-height: 30px;
  }
  .head-text{
    font-size: 13px;
    padding-bottom: 18px;
  }
}